.atr_picture-bg {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  z-index: -1;
}
.atr_picture-bg img {
  height: auto;
  width: 100%;
  object-fit: cover;
}

.atr_button {
  background-color: #d3e3f3;
  border: none;
  border-radius: 2px;
  color: #255891;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: 2px;
  margin-top: 40px;
  padding: 6px 40px;
  text-align: center;
  transition: background-color 0.2s ease;
}
.atr_button:hover {
  background-color: #f5f5f5;
}

.atr_h2 {
  color: #f5f5f5;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 0;
  text-align: center;
  font-size: 80px;
  line-height: 1.1;
}
.atr_h2 span {
  display: block;
}
.atr_h2 .atr_h-small {
  color: #f5f5f5;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 0;
  text-align: center;
  font-size: 48px;
  line-height: 1.5;
}

.atr_offleft {
  position: relative;
  right: 120px;
}

@media only screen and (max-width: 992px) {
  .atr_h2 {
    font-size: 70px;
  }
  .atr_h2 .atr_h-small {
    font-size: 42px;
  }

  .atr_offleft {
    right: 100px;
  }

  .atr_button {
    font-size: 12px;
    padding: 6px 30px;
  }
}
@media only screen and (max-width: 768px) {
  .atr_h2 {
    font-size: 60px;
  }
  .atr_h2 .atr_h-small {
    font-size: 36px;
  }

  .atr_offleft {
    right: 80px;
  }
}
@media only screen and (max-width: 576px) {
  .atr_h2 {
    font-size: 42px;
  }
  .atr_h2 .atr_h-small {
    font-size: 24px;
  }

  .atr_offleft {
    right: 60px;
  }
}
.atr_slider-container {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: nowrap;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
  overflow: hidden;
  position: relative;
}
.atr_slider-container .atr_all-slides {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.atr_slider-container .atr_slide {
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-flex-shrink: 0;
      -ms-flex-negative: 0;
          flex-shrink: 0;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  height: -webkit-fill-available;
  overflow: hidden;
  position: relative;
  transition: all 0.5s ease;
  width: 100vw;
}
.atr_slider-container .atr_slide.force-full-height {
  height: calc(var(--vh, 1vh) * 100);
}
.atr_slider-container .atr_slide h2 {
  margin: 0 240px 0 40px;
}
.atr_slider-container .atr_slider-controls {
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  left: calc(50vw + 400px);
  position: absolute;
  padding-top: 10px;
  top: 0;
}
.atr_slider-container .atr_slider-controls svg {
  border: 1px solid #f5f5f5;
  border-radius: 50%;
  color: #f5f5f5;
  cursor: pointer;
  height: 40px;
  transition: all 0.5s ease;
  vertical-align: middle;
  width: 40px;
}
.atr_slider-container .atr_slider-controls .atr_slider-btn-left {
  margin: 0 10px;
}
.atr_slider-container .atr_slider-controls .atr_slider-btn-left svg {
  background: linear-gradient(90deg, #f5f5f5 50%, transparent 0);
  background-position: 100% 100%;
  background-size: 200% 100%;
  padding: 10px 11px 10px 9px;
}
.atr_slider-container .atr_slider-controls .atr_slider-btn-left svg:hover {
  background-position: 0 100%;
  color: #255891;
}
.atr_slider-container .atr_slider-controls .atr_slider-btn-right {
  margin: 0 10px;
}
.atr_slider-container .atr_slider-controls .atr_slider-btn-right svg {
  background: linear-gradient(90deg, transparent 50%, #f5f5f5 0);
  background-position: 0 100%;
  background-size: 200% 100%;
  padding: 10px 9px 10px 11px;
}
.atr_slider-container .atr_slider-controls .atr_slider-btn-right svg:hover {
  background-position: 100% 100%;
  color: #255891;
}

@media only screen and (max-width: 1200px) {
  .atr_slider-container .atr_slider-controls {
    left: calc(50vw + 300px);
  }
}
@media only screen and (max-width: 992px) {
  .atr_slider-container .atr_slide h2 {
    margin-right: 40px;
  }
  .atr_slider-container .atr_slider-controls {
    bottom: 0;
    height: auto;
    margin-bottom: 28vh;
    left: auto;
    top: auto;
    width: 100%;
  }
}
@media only screen and (max-width: 768px) {
  .atr_slider-container .atr_slider-controls .atr_slider-btn-left svg:hover {
    background-position: 100% 100%;
    color: #f5f5f5;
  }
  .atr_slider-container .atr_slider-controls .atr_slider-btn-right svg:hover {
    background-position: 0 100%;
    color: #f5f5f5;
  }
}